import React, { Component } from "react";

import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import { Form, Input, Select } from "antd";

const { Option } = Select;
// const { TextArea } = Input;
// const InputGroup = Input.Group;

class UserUpdateForm extends Component {
  state = {
    repeat_password: null,
    button_change: false,
  }

  findMaximumSuperAdmin(arr) {
    return arr.reduce((maximum, roles) => roles.is_superadmin >= maximum ? roles.is_superadmin : maximum, arr[0].is_superadmin)
  }

  handleChangeRepeatPassword = e => {
    this.setState({
      repeat_password: null
    })
  }

  handleChangeButtonPassword = () => {
    this.setState({
      button_change: !this.state.button_change,
      repeat_password: null
    })
  }

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (values.repeat_password != values.password) {
        this.setState({
          repeat_password: "Repeat password must be same as the password"
        })
      }
      else if (!err) {
        console.log("やった")
        // console.log(values);
        this.props.submit(values);
      }
    });
  };

  render() {
    // console.log(this.props);
    const { getFieldDecorator, getFieldError } = this.props.form;
    const { auth, user } = this.props;
    const { loading, loadingSubmit } = user;
    const data = user.user ? user.user : [];
    const { roles } = auth.user ? auth.user : [];

    // define the authenticated user's superadmin
    let authSuperAdmin = null
    if (roles && roles.length > 0) {
      authSuperAdmin = this.findMaximumSuperAdmin(roles)
    }

    // define the data's superadmin
    let dataSuperAdmin = null
    if (data.roles && data.roles.length > 0) {
      dataSuperAdmin = this.findMaximumSuperAdmin(data.roles)
    }

    // define if the displayed data is the authenticated user's
    const { id_user } = auth.user ? auth.user : []; //authenticated user
    const dataIdUser = data ? data.id_user : null;
    let isMe = false;
    if (dataIdUser && id_user === dataIdUser) {
      isMe = true
    }

    // const used = data.is_used;
    const error = user.errorField ? user.errorField : [];
    // console.log(data);

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const formTextArea = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0
        },
        sm: {
          span: 24,
          offset: 0
        }
      }
    };

    return (
      <Form layout="vertical" onSubmit={this.handleSubmit}>
        {/* <Divider orientation="left" style={{ fontSize: 14 }}>
          <IntlMessages id="users.add.subtitle.data" />
        </Divider> */}
        <div className="isoDetailDataFlex" style={{ padding: "0" }}>
          <div className="isoColumn-2 isoFormGroupComp" style={{ padding: "0 15px 0 0" }}>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="users.add.username" />}
            >
              {getFieldDecorator("username", {
                initialValue: data.username,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  },
                  {
                    max: 30,
                    message: <IntlMessages id="maxLength30" />
                  }
                ]
              })(
                <Input
                  disabled={true}
                />
              )}
            </Form.Item>

            <Button
              type="secondary"
              style={{ marginBottom: "25px", width: "50%" }}
              onClick={this.handleChangeButtonPassword}
              disabled={authSuperAdmin >= dataSuperAdmin ? (
                isMe ? false : true
              ) : false}
            >
              {this.state.button_change === true
                ? <IntlMessages id="button.cancelChangePassword" />
                : <IntlMessages id="button.changePassword" />
              }

            </Button>

            {this.state.button_change === true
              ?
              (
                <div>
                  <Form.Item
                    {...formTextArea}
                    label={<IntlMessages id="users.add.newPassword" />}
                  >

                    {getFieldDecorator("password", {
                      rules: [
                        {
                          required: true,
                          message: <IntlMessages id="required" />
                        },
                        {
                          min: 6,
                          message: <IntlMessages id="minLength6" />
                        },
                      ]
                      // initialValue: data.description
                    })(<Input.Password />)}
                  </Form.Item>

                  <Form.Item
                    {...formTextArea}
                    label={<IntlMessages id="users.add.repeatNewPassword" />}
                    validateStatus={
                      this.state.repeat_password || getFieldError("repeat_password")
                        ? "error"
                        : null
                    }
                    help={
                      this.state.repeat_password
                        ? this.state.repeat_password
                        : getFieldError("repeat_password")
                    }
                  >
                    {getFieldDecorator("repeat_password", {
                      rules: [
                        {
                          required: true,
                          message: <IntlMessages id="required" />
                        },
                        {
                          min: 6,
                          message: <IntlMessages id="minLength6" />
                        },
                      ]
                      // initialValue: data.description
                    })(<Input.Password
                      onChange={this.handleChangeRepeatPassword}

                    />)}
                  </Form.Item>

                  {isMe ? (
                    <Form.Item
                      {...formTextArea}
                      label={<IntlMessages id="users.add.yourPassword" />}
                      validateStatus={
                        error.your_password || getFieldError("your_password")
                          ? "error"
                          : null
                      }
                      help={
                        error.your_password
                          ? error.your_password
                          : getFieldError("your_password")
                      }
                    >
                      {getFieldDecorator("your_password", {
                        rules: [
                          {
                            required: true,
                            message: <IntlMessages id="required" />
                          },
                        ]
                        // initialValue: data.description
                      })(<Input.Password
                      //onChange={this.handleChangeRepeatPassword}
                      />)}
                    </Form.Item>

                  ) : null}
                </div>
              )
              : null
            }
            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="users.add.role" />}
            >
              {getFieldDecorator("id_role", {
                initialValue: data.id_role,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  }
                ]
              })(
                <Select
                  mode="multiple"
                  loading={this.props.user.loadingFilterRoles}
                  showSearch
                  optionFilterProp="children"
                //onChange={this.handleChangeRole}
                //disabled={used ? true : false}
                >
                  {this.props.user.roles.map((data, key) => (
                    <Option key={key} value={data.id_role}>
                      {data.role_name}
                    </Option>
                  ))}
                </Select>
              )}
            </Form.Item>

          </div>
        </div>

        <Form.Item {...tailFormItemLayout}>
          <Button
            type="primary"
            htmlType="submit"
            style={{ marginTop: "20px", marginLeft: "50px" }}
            loading={loading || loadingSubmit}
          >
            <IntlMessages id="button.submit" />
          </Button>
        </Form.Item>
      </Form>
    );
  }
}

const WrappedUserUpdateForm = Form.create({
  name: "update_user"
})(UserUpdateForm);

export default WrappedUserUpdateForm;
