import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
// import { Redirect } from "react-router-dom";

import UserUpdateForm from "./updateForm";

import notification from "../../../../components/notification";
import { Spin } from "antd";

import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/employee/user/actions";
import { Box } from "../../../App/app.style";

const redirectTo = "/dashboard/setup/users"

class UpdateUser extends Component {
  // Get All data diawal
  componentDidMount() {
    const { editUser } = this.props;
    const { id } = this.props.match.params;
    editUser(id);
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.user.error) {
      notification("error", this.props.user.error);
      this.props.clearError();
    }

    if (this.props.user.success) {
      notification("success", this.props.user.success);
      this.props.clearSuccess();
      this.props.history.replace(redirectTo)
    }
  }

  handleSubmit = value => {
    const { updateUser } = this.props;
    const { id } = this.props.match.params;

    // Uppercasing supplier type code
    // value.user_code = value.user_code.toUpperCase();
    updateUser(id, value);
  };

  render() {
    // console.log(this.props.user);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader title={<IntlMessages id="users.update.title" />}
            style={{ margin: 0 }}
          >
            <Button onClick={() => this.props.history.replace(redirectTo)}>
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          {this.props.user.loading &&
            !this.props.user.loadingSubmit ? (
              <div className="spinStyle">
                <Spin size="large" />
              </div>
            ) : (
              <UserUpdateForm
                auth={this.props.auth}
                submit={this.handleSubmit}
                user={this.props.user}
              />
            )}
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    auth: state.auth,
    user: state.user
  };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(UpdateUser);
